import React from 'react';
import { connect } from 'react-redux';
import { Card } from 'material-ui/Card';
import { Row, Col } from 'react-flexbox-grid-aphrodite';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import FlatButton from 'material-ui/FlatButton';
import CircularProgress from 'material-ui/CircularProgress';
import Dialog from 'material-ui/Dialog';

import BaseComponent from 'components/base/BaseComponent';
import {
  Global as GlobalStrings,
  Profile as userProfileStrings
} from 'i18n/Persian';
export class PasswordAndEmail extends BaseComponent {
  render() {
    return (
      <div>
        {this.renderUI()}
        {this.renderHelperUI()}
      </div>
    );
  }
  renderUI = () => {
    return (
      <div
        className={
          'childComponent container ' + this.props.states.mainViewClassName
        }
      >
        <Row center="xs">
          <Col lg={6} md={6} sm={6} xs={12}>
            <Card>
              <Row style={{ margin: 0 }}>
                <Col
                  lg={6}
                  md={5}
                  sm={5}
                  xs={12}
                  style={
                    this.props.windowWidth > 767
                      ? { backgroundColor: 'whitesmoke', padding: 16 }
                      : { padding: 16 }
                  }
                >
                  {this.renderPasswordChangeSection()}
                </Col>
                <Col lg={6} md={7} sm={7} xs={12} style={{ padding: 16 }}>
                  {this.renderEmailChangeSection()}
                </Col>
              </Row>
            </Card>
          </Col>
        </Row>
      </div>
    );
  };

  renderHelperUI = () => {
    return (
      <Dialog
        title={userProfileStrings.passwordConfrimDialogTitle}
        titleStyle={{ textAlign: 'left', direction: 'ltr', fontSize: '1.2rem' }}
        bodyStyle={{ textAlign: 'center', direction: 'ltr', fontSize: '.9rem' }}
        actions={
          this.props.states.isUpdating
            ? <CircularProgress size={25} style={{ top: 4, marginRight: 8 }} />
            : [
                <FlatButton
                  label={GlobalStrings.confirm}
                  primary={true}
                  onTouchTap={() =>
                    this.props.handlers.onConfirmUpdateUserPrivacy()}
                />,
                <FlatButton
                  label={GlobalStrings.close}
                  primary={true}
                  onTouchTap={() => this.props.handlers.onCloseDialog()}
                />
              ]
        }
        modal={this.props.states.isUpdating}
        open={this.props.states.isDialogOpen}
        onRequestClose={() => this.props.handlers.onCloseDialog()}
        contentStyle={{ width: this.props.windowWidth > 767 ? '30%' : '90%' }}
      >
        {this.renderDialogBody()}
      </Dialog>
    );
  };

  renderDialogBody = () => {
    return (
      <Row>
        <Col xs={12}>
          <Row start="xs">
            <Col xs={12}>
              {userProfileStrings.passwordConfrimDialogBody}
            </Col>
            <Col lg={4} md={6} sm={6} xs={4}>
              <TextField
                floatingLabelText={userProfileStrings.password}
                value={this.props.states.user.password}
                type="password"
                autoFocus={true}
                onChange={e => {
                  this.props.errorText.newPassword = '';
                  this.props.handlers.onEnterPrivacyData(
                    'password',
                    e.target.value
                  );
                }}
                onKeyPress={e => {
                  this.handleKeyPress(e);
                }}
                style={{
                  width: '100%',
                  fontFamily: 'iransans',
                  fontSize: '0.9rem'
                }}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    );
  };
  /// to handle enter key press
  handleKeyPress = e => {
    if (e.key === 'Enter') {
      this.props.handlers.onConfirmUpdateUserPrivacy();
    }
  };

  renderPasswordChangeSection = () => {
    return (
      <div>
        <Row>تغییر کلمه عبور</Row>
        <Row>
          <Col lg={11} md={11} sm={11} xs={7}>
            <TextField
              floatingLabelText={userProfileStrings.newPassword}
              type="password"
              ref="newPasswordInput"
              value={
                this.props.states.user.privacy.newPassword
                  ? this.props.states.user.privacy.newPassword
                  : ''
              }
              errorText={this.props.errorText.newPassword}
              onChange={e => {
                this.props.errorText.newPassword = '';
                this.props.handlers.onEnterPrivacyData(
                  'newPassword',
                  e.target.value
                );
              }}
              onBlur={() => this.props.handlers.verifyNewPassword()}
              style={{
                width: '100%',
                fontFamily: 'iransans',
                fontSize: '0.9rem'
              }}
            />
          </Col>
          <Col lg={11} md={11} sm={11} xs={7}>
            <TextField
              floatingLabelText={userProfileStrings.reNewPassword}
              type="password"
              ref="confrimNewPasswordInput"
              value={
                this.props.states.user.privacy.confirmNewPassword
                  ? this.props.states.user.privacy.confirmNewPassword
                  : ''
              }
              errorText={this.props.errorText.confirmNewPassword}
              onChange={e => {
                this.props.errorText.confirmNewPassword = '';
                this.props.handlers.onEnterPrivacyData(
                  'confirmNewPassword',
                  e.target.value
                );
              }}
              onBlur={() => this.props.handlers.verifyNewPassword()}
              style={{
                width: '100%',
                fontFamily: 'iransans',
                fontSize: '0.9rem'
              }}
            />
          </Col>
        </Row>
      </div>
    );
  };
  renderEmailChangeSection = () => {
    return (
      <div>
        <Row>تغییر ایمیل</Row>
        <Row start="xs">
          <Col lg={11} md={11} sm={11} xs={9}>
            <TextField
              floatingLabelText={userProfileStrings.email}
              value={this.props.states.user.privacy.email}
              type="email"
              ref="emailInput"
              errorText={this.props.errorText.email}
              onChange={e => {
                this.props.errorText.email = '';
                this.props.handlers.onEnterPrivacyData('email', e.target.value);
              }}
              onBlur={() => this.props.handlers.verifyEmail()}
              style={{
                width: '100%',
                fontFamily: 'iransans',
                fontSize: '0.9rem'
              }}
            />
          </Col>
        </Row>
        <Row end="xs">
          <Col
            xs={12}
            style={
              this.props.windowWidth > 767
                ? { position: 'absolute', bottom: '8px' }
                : { marginTop: '8px' }
            }
          >
            <RaisedButton
              label={userProfileStrings.save}
              className={this.props.states.buttonStyle}
              primary={true}
              onTouchTap={event => {
                this.refs.emailInput.blur();
                this.refs.confrimNewPasswordInput.blur();
                this.refs.newPasswordInput.blur();
                this.props.handlers.onUpdateUserPrivacy();
              }}
            />
          </Col>
        </Row>
      </div>
    );
  };
}
export default connect(state => {
  return {
    windowWidth: state.app.windowWidth
  };
})(PasswordAndEmail);



