import React from 'react';
import { Card } from 'material-ui/Card';
import { Row, Col } from 'react-flexbox-grid-aphrodite';
import SvgIcon from 'material-ui/SvgIcon';

import FloatingActionButton from 'material-ui/FloatingActionButton';

import BaseComponent from 'components/base/BaseComponent';
import Table from 'components/table/Table';
import CircularProgress from 'material-ui/CircularProgress';
import { NO_DATA } from 'components/StatesIcons';
import { Profile as userProfileStrings } from 'i18n/Persian';
const UploadIcon = props =>
  <SvgIcon {...props}>
    <path d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z" />
  </SvgIcon>;

export class SelectEducationalGroup extends BaseComponent {
  render() {
    return (
      <div>
        {this.props.states.isUpdating ? this.renderLoading() : this.renderUI()}
      </div>
    );
  }
  renderLoading = () => {
    return (
      <div
        style={{
          width: '100%',
          heigth: '100%',
          backgroundColor: 'rgba(0,0,0,0.5)',
          position: 'fixed',
          top: 0,
          right: 0,
          left: 0,
          bottom: 0,
          zIndex: 1301
        }}
      >
        <CircularProgress
          style={{
            position: 'absolute',
            left: 0,
            right: 0,
            top: 0,
            bottom: 0,
            margin: 'auto'
          }}
          size={60}
          thickness={5}
        />
      </div>
    );
  };
  renderUI = () => {
    if (this.props.courseList && this.props.courseList.length > 0) {
      var counter = 1;
      var items = this.props.courseList.map(item => {
        delete item.row;
        return { row: counter++, ...item };
      });
    }
    return (
      <div className="childComponent container">
        {this.props.courseList && this.props.courseList.length > 0
          ? <div className={this.props.states.mainViewClassName}>
              <Row center="xs">
                <Col xs={12}>
                  <Card style={{ position: 'relative' }}>
                    <Table
                      headItems={this.props.headItems}
                      items={items}
                      hasSearch={false}
                      showCheckbox={true}
                      onTableBodyCheckboxClick={contact =>
                        this.props.handlers.onClickTableCheckBox(contact)}
                      onDelete={() => {
                        this.props.handlers.onRemoveItem();
                      }}
                      onTableHeaderCheckboxClick={selectAll => {
                        this.props.handlers.onClickTableHeaderCheckBox(
                          selectAll
                        );
                      }}
                      paginationDropdownClassName="tablePaginationDropdown"
                      excludedColumnsOnSmallScreen={[
                        'row',
                        'lessonDateTime',
                        'firstName'
                      ]}
                    />
                  </Card>
                </Col>
              </Row>
            </div>
          : <Row center="xs">
              <Col lg={4} md={4} sm={6} xs={11} style={{ marginTop: '10%' }}>
                {NO_DATA}
                <p
                  className="noMessage"
                  style={{ fontSize: '.8rem', color: '#999999', marginTop: 16 }}
                >
                  {userProfileStrings.courseListEmpty}
                </p>
              </Col>
            </Row>}
        {this.renderFabButton()}
      </div>
    );
  };

  renderFabButton = () => {
    return (
      <FloatingActionButton
        className={this.props.states.fabClassName}
        mini={true}
        onTouchTap={event => {
          this.props.handlers.onUpdateCourseList();
        }}
        style={{
          margin: 0,
          top: 'auto',
          bottom: 20,
          left: 'auto',
          position: 'fixed',
          zIndex: 999
        }}
      >
        <UploadIcon color="#ffffff" />
      </FloatingActionButton>
    );
  };
}
export default SelectEducationalGroup;



