import React from 'react';
import { connect } from 'react-redux';
import { Row, Col } from 'react-flexbox-grid-aphrodite';
import Divider from 'material-ui/Divider';
import IconButton from 'material-ui/IconButton';
import SvgIcon from 'material-ui/SvgIcon';
import CircularProgress from 'material-ui/CircularProgress';
import Drawer from 'material-ui/Drawer';
import Snackbar from 'material-ui/Snackbar';
import IconMenu from 'material-ui/IconMenu';
import MenuItem from 'material-ui/MenuItem';
import ArrowDropDownIcon from 'material-ui/svg-icons/navigation/arrow-drop-down';
import { Link } from 'react-router';
import TouchRipple from 'material-ui/internal/TouchRipple';

import SelectCourse from './SelectCourse';
import SelectEducationalGroup from './SelectEducationalGroup';
import PasswordAndEmail from './PasswordAndEmail';
import Setting from './Setting';
import PersonalInformation from './PersonalInformation';
import BaseComponent from 'components/base/BaseComponent';
import Appbar from 'components/appbar/Appbar';
import Loading from 'components/loading/Loading';
import {
  Global as GlobalStrings,
  Profile as userProfileStrings
} from 'i18n/Persian';
import { OPERATION_FAILED } from 'components/StatesIcons';

const Back = props =>
  <SvgIcon {...props}>
    <path d="M21,11H6.83L10.41,7.41L9,6L3,12L9,18L10.41,16.58L6.83,13H21V11Z" />
  </SvgIcon>;

const Refresh = props =>
  <SvgIcon {...props}>
    <path d="M17.65,6.35C16.2,4.9 14.21,4 12,4A8,8 0 0,0 4,12A8,8 0 0,0 12,20C15.73,20 18.84,17.45 19.73,14H17.65C16.83,16.33 14.61,18 12,18A6,6 0 0,1 6,12A6,6 0 0,1 12,6C13.66,6 15.14,6.69 16.22,7.78L13,11H20V4L17.65,6.35Z" />
  </SvgIcon>;

const Account = props =>
  <SvgIcon {...props}>
    <path d="M12,4A4,4 0 0,1 16,8A4,4 0 0,1 12,12A4,4 0 0,1 8,8A4,4 0 0,1 12,4M12,14C16.42,14 20,15.79 20,18V20H4V18C4,15.79 7.58,14 12,14Z" />
  </SvgIcon>;

const Verified = props =>
  <SvgIcon {...props}>
    <path d="M10,17L6,13L7.41,11.59L10,14.17L16.59,7.58L18,9M12,1L3,5V11C3,16.55 6.84,21.74 12,23C17.16,21.74 21,16.55 21,11V5L12,1Z" />
  </SvgIcon>;

const Library = props =>
  <SvgIcon {...props}>
    <path d="M12,8A3,3 0 0,0 15,5A3,3 0 0,0 12,2A3,3 0 0,0 9,5A3,3 0 0,0 12,8M12,11.54C9.64,9.35 6.5,8 3,8V19C6.5,19 9.64,20.35 12,22.54C14.36,20.35 17.5,19 21,19V8C17.5,8 14.36,9.35 12,11.54Z" />
  </SvgIcon>;

const Settings = props =>
  <SvgIcon {...props}>
    <path d="M12,15.5A3.5,3.5 0 0,1 8.5,12A3.5,3.5 0 0,1 12,8.5A3.5,3.5 0 0,1 15.5,12A3.5,3.5 0 0,1 12,15.5M19.43,12.97C19.47,12.65 19.5,12.33 19.5,12C19.5,11.67 19.47,11.34 19.43,11L21.54,9.37C21.73,9.22 21.78,8.95 21.66,8.73L19.66,5.27C19.54,5.05 19.27,4.96 19.05,5.05L16.56,6.05C16.04,5.66 15.5,5.32 14.87,5.07L14.5,2.42C14.46,2.18 14.25,2 14,2H10C9.75,2 9.54,2.18 9.5,2.42L9.13,5.07C8.5,5.32 7.96,5.66 7.44,6.05L4.95,5.05C4.73,4.96 4.46,5.05 4.34,5.27L2.34,8.73C2.21,8.95 2.27,9.22 2.46,9.37L4.57,11C4.53,11.34 4.5,11.67 4.5,12C4.5,12.33 4.53,12.65 4.57,12.97L2.46,14.63C2.27,14.78 2.21,15.05 2.34,15.27L4.34,18.73C4.46,18.95 4.73,19.03 4.95,18.95L7.44,17.94C7.96,18.34 8.5,18.68 9.13,18.93L9.5,21.58C9.54,21.82 9.75,22 10,22H14C14.25,22 14.46,21.82 14.5,21.58L14.87,18.93C15.5,18.67 16.04,18.34 16.56,17.94L19.05,18.95C19.27,19.03 19.54,18.95 19.66,18.73L21.66,15.27C21.78,15.05 21.73,14.78 21.54,14.63L19.43,12.97Z" />
  </SvgIcon>;
const Close = props =>
  <SvgIcon {...props}>
    <path d="M19,6.41L17.59,5L12,10.59L6.41,5L5,6.41L10.59,12L5,17.59L6.41,19L12,13.41L17.59,19L19,17.59L13.41,12L19,6.41Z" />
  </SvgIcon>;

const Search = props =>
  <SvgIcon {...props}>
    <path d="M9.5,3A6.5,6.5 0 0,1 16,9.5C16,11.11 15.41,12.59 14.44,13.73L14.71,14H15.5L20.5,19L19,20.5L14,15.5V14.71L13.73,14.44C12.59,15.41 11.11,16 9.5,16A6.5,6.5 0 0,1 3,9.5A6.5,6.5 0 0,1 9.5,3M9.5,5C7,5 5,7 5,9.5C5,12 7,14 9.5,14C12,14 14,12 14,9.5C14,7 12,5 9.5,5Z" />
  </SvgIcon>;

const Menu = props =>
  <SvgIcon {...props}>
    <path d="M3,6H21V8H3V6M3,11H21V13H3V11M3,16H21V18H3V16Z" />
  </SvgIcon>;

const Logout = props =>
  <SvgIcon {...props}>
    <path d="M16.56,5.44L15.11,6.89C16.84,7.94 18,9.83 18,12A6,6 0 0,1 12,18A6,6 0 0,1 6,12C6,9.83 7.16,7.94 8.88,6.88L7.44,5.44C5.36,6.88 4,9.28 4,12A8,8 0 0,0 12,20A8,8 0 0,0 20,12C20,9.28 18.64,6.88 16.56,5.44M13,3H11V13H13" />
  </SvgIcon>;

export class ProfileUI extends BaseComponent {
  render() {
    return (
      <div>
        {this.renderUI()}
      </div>
    );
  }
  renderUI = () => {
    return (
      <div>
        {this.renderAppbar()}
        <div
          className="component"
          style={
            this.props.windowWidth > 767
              ? { marginTop: 72 }
              : this.props.states.activeStep === 4
                ? { marginTop: 128 }
                : { marginTop: 72 }
          }
        >
          {this.renderDrawer()}
          {this.renderSelectedComponent()}
        </div>
        {this.renderHelperComponents()}
      </div>
    );
  };
  renderAppbar = () => {
    return (
      <Appbar
        className={this.props.states.appbarClassName}
        bodyContainerClassName="appbarBody"
        rightSection={
          this.props.windowWidth < 992
            ? <div>
                <IconButton
                  style={{ top: 8 }}
                  onTouchTap={() => {
                    this.props.handlers.onMenuButtonClick();
                  }}
                >
                  <Menu />
                </IconButton>
                <span>
                  {userProfileStrings.profile}
                </span>
              </div>
            : void 0
        }
        leftSection={this.renderAppbarActionButtons()}
        body={this.renderAppbarBody()}
      />
    );
  };

  renderAppbarActionButtons = () => {
    var actionButtons = [
      <IconButton
        onTouchTap={() => setTimeout(this.props.handlers.onBackTouchTap, 200)}
      >
        <Back />
      </IconButton>,
      <IconButton
        onTouchTap={() =>
          setTimeout(this.props.handlers.onRefreshTouchTap, 200)}
      >
        <Refresh />
      </IconButton>
    ];

    return actionButtons;
  };

  /**
  * @description : to put search bar in appbar
  *
  * @author Soorena Sanagoo
  **/
  renderAppbarBody = () => {
    if (this.props.states.activeStep === 4) {
      return (
        <div style={{ position: 'relative' }}>
          <div style={{ position: 'relative', marginTop: 12 }}>
            <IconButton
              style={{
                margin: 0,
                padding: 0,
                position: 'absolute',
                top: 0,
                left: 0
              }}
            >
              {this.props.states.searchText &&
              this.props.states.searchText.length > 0
                ? <Close
                    color="#3a3a3a"
                    onTouchTap={this.props.handlers.onClearSearchInput}
                  />
                : <Search color="#abaaaa" />}
            </IconButton>
            <input
              value={this.props.states.searchText}
              placeholder={
                this.props.type === 2
                  ? userProfileStrings.courseSearch
                  : userProfileStrings.groupSearch
              }
              className="searchInput"
              style={{ paddingLeft: 32 }}
              onChange={e => this.props.handlers.onPerformSearch(e)}
              onKeyPress={e => {
                this.handleKeyPress(e);
              }}
              ref="searchInput"
            />
            {this.props.states.isSearching
              ? <CircularProgress
                  style={{ position: 'absolute', right: '8px', top: '10px' }}
                  size={20}
                  thickness={2.5}
                />
              : void 0}
          </div>
          {this.renderShowSearchResult()}
        </div>
      );
    } else {
      return void 0;
    }
  };

  /// to handle enter key press on search in step 4
  handleKeyPress = e => {
    if (e.key === 'Enter') {
      if (this.props.states.searchResult.length === 1) {
        this.props.handlers.onAddItem(this.props.states.searchResult[0]);
      }
    } else return e.key;
  };

  /**
  * @description : for show search result div if search result has item
  *
  * @author Soorena Sanagoo
  **/
  renderShowSearchResult = () => {
    if (this.props.states.showSearchResult) {
      return (
        <div
          style={{
            top: 42,
            zIndex: 400,
            width: '100%',
            position: 'absolute',
            background: 'white',
            boxShadow:
              'rgba(0, 0, 0, 0.117647) 0px 1px 6px,rgba(0, 0, 0, 0.117647) 0px 1px 4px'
          }}
        >
          {this.renderHtmlTable()}
        </div>
      );
    }
  };
  /**
  * @description : to make html table from search result data
  *
  * @author Soorena Sanagoo
**/
  renderHtmlTable = () => {
    if (this.props.states.searchResult.length > 0) {
      var result = this.props.states.searchResult.map(item => {
        //map on array to get object(item)
        var id = item.id + 1;
        return (
          <div
            key={item.id}
            style={{ cursor: 'pointer' }}
            onTouchTap={event => {
              this.props.handlers.onAddItem(item);
              this.refs.searchInput.focus();
            }}
          >
            <Row style={{ padding: '16px' }}>
              {Object.keys(item).map(
                // map on object's key to get keyName
                keyName => {
                  if (keyName !== 'id') {
                    return (
                      <Col
                        key={id++}
                        lg={4}
                        md={6}
                        sm={6}
                        xs={12}
                        style={{ padding: 4 }}
                      >
                        <span className="searchResultCol truncate">
                          {this.props.type === 2
                            ? userProfileStrings.course[keyName]
                            : userProfileStrings.educationalGroup[keyName]}
                          : {item[keyName]}
                        </span>
                      </Col>
                    );
                  } else {
                    return void 0;
                  }
                }
              )}
            </Row>
            <Divider />
          </div>
        );
      });
      return (
        <div>
          {result}
        </div>
      );
    } else {
      return (
        <div style={{ padding: 8, textAlign: 'center' }}>
          {userProfileStrings.dataNotFound}
        </div>
      );
    }
  };

  renderDrawer = () => {
    return (
      <div>
        <Drawer
          disableSwipeToOpen={false}
          containerClassName={
            this.props.windowWidth > 992
              ? this.props.states.drawerClassName
              : ''
          }
          open={this.props.states.drawerOpen}
          containerStyle={{ background: 'whitesmoke' }}
          docked={this.props.windowWidth > 992 ? true : false}
          onRequestChange={drawer =>
            this.props.handlers.onDrawerRequestChange(drawer)}
        >
          <div
            style={{
              padding: 16,
              paddingTop: 8,
              borderBottom: '1px solid #dedcdc'
            }}
          >
            <h3 style={{ fontSize: '1rem', marginTop:0, color: '#707070' }}>
              {userProfileStrings.drawer.settings}
              <IconMenu
                iconButtonElement={
                  <IconButton
                    onTouchTap={() => {
                      this.setState({ isDrawerIconButtonOpen: true });
                    }}
                  >
                    <ArrowDropDownIcon color={'#7f7e7e'} />
                  </IconButton>
                }
                anchorOrigin={{ horizontal: 'left', vertical: 'top' }}
                targetOrigin={{ horizontal: 'left', vertical: 'top' }}
                style={{ top: 8 }}
              >
                <Link to="/accounts" style={{ textDecoration: 'none' }}>
                  <MenuItem
                    className="drawerDropDownItem"
                    primaryText={userProfileStrings.changeAccounts}
                    leftIcon={<Settings />}
                  />
                </Link>
                <MenuItem
                  onTouchTap={this.props.handlers.handleLogout}
                  style={{ textAlign: 'right' }}
                  className="drawerDropDownItem"
                  primaryText={userProfileStrings.logout}
                  leftIcon={<Logout />}
                />
              </IconMenu>
            </h3>
            <p style={{ fontSize: '.8rem', color: '#a1a1a1' }}>
              {userProfileStrings.drawer.settingsDescription}
            </p>
          </div>

          <div
            className={
              this.props.states.activeStep === 1 ? 'activeMenu' : 'menu'
            }
            onTouchTap={() => {
              this.props.handlers.onMenuClick(1);
            }}
          >
            <TouchRipple>
              <div className="menuBox">
                <Account color="#707070" />
                <h2>
                  {userProfileStrings.drawer.personalInformation}
                </h2>
                <p className="menuDescription">
                  {userProfileStrings.drawer.personalInformationDescription}
                </p>
              </div>
            </TouchRipple>  
          </div>
          
          <div
            className={
              this.props.states.activeStep === 2 ? 'activeMenu' : 'menu'
            }
            onTouchTap={() => {
              this.props.handlers.onMenuClick(2);
            }}
          >
            <TouchRipple>
              <div className="menuBox">
                <Verified color="#707070" />
                
                <h2>
                  {userProfileStrings.drawer.passwordAndEmail}
                </h2>
                <p className="menuDescription">
                  {userProfileStrings.drawer.passwordAndEmailDescription}
                </p>

              </div>
            </TouchRipple>
          </div>
          <div
            className={
              this.props.states.activeStep === 3 ? 'activeMenu' : 'menu'
            }
            onTouchTap={() => this.props.handlers.onMenuClick(3)}
          >
            <TouchRipple>
              <div className="menuBox">
                <Settings color="#707070" />
                <h2>
                  {userProfileStrings.drawer.applicationSettings}
                </h2>
                <p className="menuDescription">
                  {userProfileStrings.drawer.applicationSettingsDescription}
                </p>
              </div>
            </TouchRipple>
          </div>
          {this.props.type === 2
            ? <div
                className={
                  this.props.states.activeStep === 4 ? 'activeMenu' : 'menu'
                }
                onTouchTap={() => this.props.handlers.onMenuClick(4)}
              >
                <TouchRipple>
                  <div className="menuBox">
                    <Library color="#707070" />
                    <h2>
                      {userProfileStrings.drawer.selectCourse}
                    </h2>
                    <p className="menuDescription">
                      {userProfileStrings.drawer.selectCourseDescription}
                    </p>
                  </div>
                </TouchRipple>
              </div>
            : this.props.type === 1
              ? <div
                  className={
                    this.props.states.activeStep === 4 ? 'activeMenu' : 'menu'
                  }
                  onTouchTap={() => this.props.handlers.onMenuClick(4)}
                >
                  <TouchRipple>
                    <div className="menuBox">
                      <Library color="#707070" />
                      <h2>
                        {userProfileStrings.drawer.selectEducationalGroup}
                      </h2>
                      <p className="menuDescription">
                        {
                          userProfileStrings.drawer
                            .selectEducationalGroupDescription
                        }
                      </p>
                    </div>
                  </TouchRipple>
                </div>
              : void 0}
        </Drawer>
      </div>
    );
  };

  /**
  * @description : handler for show right componet
  *
  * @author Soorena Sanagoo
  **/
  renderSelectedComponent = () => {
    if (this.props.states.isFetchError) {
      return (
        <Row center="xs" className="childComponent">
          <Col lg={4} md={4} sm={6} xs={11} style={{ marginTop: '10%' }}>
            {OPERATION_FAILED}
            <p
              className="noMessage"
              style={{ fontSize: '.8rem', color: '#999999', marginTop: 16 }}
            >
              {GlobalStrings.fatalError}
            </p>
          </Col>
        </Row>
      );
    } else if (this.props.states.isLoading) {
      return (
        <div className="childComponent">
          <Loading style={{ marginTop: '10%' }} />
        </div>
      );
    } else {
      if (this.props.states.activeStep === 1) {
        return this.renderPersonalInformation();
      } else if (this.props.states.activeStep === 2) {
        return this.renderEmailAndPassword();
      } else if (this.props.states.activeStep === 3) {
        return this.renderSettings();
      } else if (this.props.states.activeStep === 4) {
        if (this.props.type === 1) {
          return this.renderSelectEducationalGroup();
        } else if (this.props.type === 2) {
          return this.renderSelectCourse();
        }
      }
    }
  };

  renderHelperComponents = () => {
    return (
      <Snackbar
        className="snack"
        open={this.props.states.isSnackOpen}
        message={this.props.states.snackMessage}
        autoHideDuration={3000}
        onRequestClose={this.props.handlers.onCloseSnackbar}
      />
    );
  };

  renderPersonalInformation = () => {
    return (
      <PersonalInformation
        userInfo={this.props.states.user.info}
        handlers={this.props.handlers}
        errorText={this.props.states.errorText}
        states={this.props.states}
        userFirstName={
          this.props.serverUserInformation.info.firstName
            ? this.props.serverUserInformation.info.firstName.substring(0, 1)
            : 'ن'
        }
      />
    );
  };

  renderEmailAndPassword = () => {
    return (
      <PasswordAndEmail
        states={this.props.states}
        handlers={this.props.handlers}
        errorText={this.props.states.errorText}
      />
    );
  };

  renderSettings = () => {
    return (
      <Setting
        handlers={this.props.handlers}
        states={this.props.states}
        minutes={this.props.minutes}
        messageFetchLimit={this.props.messageFetchLimit}
      />
    );
  };

  renderSelectEducationalGroup = () => {
    return (
      <SelectEducationalGroup
        educationalGroupList={this.props.states.user.selectEducationalGroup}
        headItems={this.props.headItems}
        handlers={this.props.handlers}
        states={this.props.states}
      />
    );
  };

  renderSelectCourse = () => {
    return (
      <SelectCourse
        courseList={this.props.states.user.selectedCourses}
        headItems={this.props.headItems}
        handlers={this.props.handlers}
        states={this.props.states}
      />
    );
  };
}

export default connect(state => {
  return {
    type: state.user.type
  };
})(ProfileUI);



