import React from 'react';
import { connect } from 'react-redux';
import { hashHistory } from 'react-router';
import MenuItem from 'material-ui/MenuItem';
import cookie from 'react-cookie';

import BaseComponent from 'components/base/BaseComponent';
import ProfileUI from './ProfileUI';
import {
  fetchUserProfile,
  search,
  updateUserInfo,
  updateUserPrivacy,
  updateUserSettings,
  updateUserCourses,
  updateUserEducationalGroups
} from 'api/Board';
import {
  isAlphabet,
  validateEmail,
  validateMobile,
  compareObject,
  convertEnDigits
} from 'helpers/Helpers.js';
import {
  Global as GlobalStrings,
  Profile as userProfileStrings
} from 'i18n/Persian';
import { updateUser } from 'redux/actions/UserActions';
import { logout } from 'redux/actions/AppActions';
import './Style.css';
const MIN_FIRSTNAME_LENGTH = 3;
const MIN_LASTNAME_LENGTH = 3;
const PASSWORD_MIN_LENGTH = 8;
const MIN_SEARCH_LENGTH = 2;
const ENTERING_YEAR_LENGTH = 4;

export class Profile extends BaseComponent {
  constructor(props) {
    super(props);
    this.serverUserInformation = undefined;
    this.headItems = {};
    this.minutes = [];
    for (let i = 15; i <= 60; i += 15) {
      this.minutes.push(
        <MenuItem
          value={i}
          key={i}
          primaryText={convertEnDigits(i) + userProfileStrings.minute}
        />
      );
    }
    this.messageFetchLimit = [];
    for (let i = 10; i <= 50; i += 10) {
      this.messageFetchLimit.push(
        <MenuItem value={i} key={i} primaryText={convertEnDigits(i)} />
      );
    }
    this.DEFAULT_STATES = {
      mainViewClassName: 'bottom-to-top-move-in', // To hold main view's class name,
      appbarClassName: 'top-to-bottom-move-in', // To hold appbar's class name
      drawerClassName: 'right-to-left-move-in', // to hold drawer's class name
      fabClassName: 'right-to-left-move-in', // to hold fab class name
      isLoading: true, // If true then component will show a loading
      isUpdating: false, // if true then component will show a loading
      isSearching: false, // if true then component will show loading
      isFetchError: false, // if true then component will shoe operation Failed
      searchText: '', // hold search text in selectcourse and select educational group
      showSearchResult: false, // if true then show search result section
      searchResult: [], // to hold search result data that come from server
      idsToBeRemoved: [], // to holds items id that will remove
      drawerOpen: false, // to toggle drawer
      buttonStyle: '', // to show pulse style when information change
      isDialogOpen: false, // if true show dialog
      user: {
        // user infomation
        info: {
          firstName: '',
          lastName: '',
          mobile: '',
          gender: '',
          avatar: '',
          semester: '',
          year: '',
          universityName: ''
        },
        privacy: {
          newPassword: '',
          confirmNewPassword: '',
          email: ''
        },
        selectedCourses: [],
        selectEducationalGroup: [],
        settings: {
          messageFetchLimit: 10,
          showAllMessageFromAllAccount: false,
          timeForAutomaticallyLogout: 600000
        },
        password: '', // to hold users password
        avatarFile: {} // hold user selected avatar file
      },
      errorText: {
        // for show error message to user
        firstName: '',
        lastName: '',
        mobile: '',
        email: '',
        newPassword: '',
        confirmNewPassword: '',
        enteringYear: ''
      },
      activeStep: 1, // to hold step
      isSnackOpen: false, //if true show snackbar
      snackMessage: '' //to hold snackbar message
    };

    // To hold all of the callback handlers
    this.handlers = {
      onRefreshTouchTap: this.handleRefreshTouchTap,
      onAppbarRightSectionTouchTap: this.handleBackTouchTap,
      onBackTouchTap: this.handleBackTouchTap,
      onAvatarChange: this.handleAvatarChange,
      onRemoveAvatar: this.handleRemoveAvatar,
      onMenuButtonClick: this.handleMenuButtonClick,
      onDrawerRequestChange: this.handleDrawerRequestChange,
      onSelectCourseBackTouchTap: this.handleSelectCourseBackTouchTap,
      onCloseSnackbar: this.handleCloseSnackbar,
      handleLogout: this.handleLogout,
      onMenuClick: this.handleMenuClick,
      onEnterPersonalData: this.handleEnterPersonalData,
      onEnterSettingsData: this.handleEnterSettingsData,
      onEnterPrivacyData: this.handleEnterPrivacyData,
      onClickSelectCourse: this.handleClickSelectCourse,
      onPerformSearch: this.handlePerfomSearch,
      onAddItem: this.handleAddItem,
      onClearSearchInput: this.handleClearSearchInput,
      onUpdateUserInfo: this.handleUpdateUserInfo,
      onUpdateUserPrivacy: this.handleUpdateUserPrivacy,
      onConfirmUpdateUserPrivacy: this.handleConfirmUserPrivacy,
      onCloseDialog: this.handleCloseDialog,
      onUpdateUserSettings: this.handleUpdateUserSettings,
      onUpdateCourseList: this.handleUpdateCourseList,
      onUpdateEducationalGroupList: this.handleUpdateEducationalGroupList,
      verifyEmail: this.verifyEmail,
      verifyEnteringYear: this.verifyEnteringYear,
      verifyFirstName: this.verifyFirstName,
      verifyLastName: this.verifyLastName,
      verifyPhoneNumber: this.verifyPhoneNumber,
      verifyNewPassword: this.verifyNewPassword,
      verifyConfirmNewPassword: this.verifyConfirmNewPassword,
      onClickTableCheckBox: this.handleClickTableCheckBox,
      onRemoveItem: this.handleRemoveItem,
      onClickTableHeaderCheckBox: this.handleClickTableHeaderCheckBox
    };

    this.state = this.DEFAULT_STATES;
  }

  componentDidMount() {
    this.load();
  }

  componentWillUpdate(nextProps, nextState) {
    process.env.NODE_ENV === 'development'
      ? console.log('New Component states =>', nextState)
      : void 0;
    if (this.serverUserInformation !== undefined) {
      this.compareObject(nextState);
    }
    if (nextProps.windowWidth > 992) {
      if (!this.state.drawerOpen) {
        this.setState({
          drawerOpen: true
        });
      }
    }
    if (nextProps.windowWidth < 992) {
      if (this.state.drawerOpen) {
        this.setState({
          drawerOpen: false
        });
      }
    }
  }
  componentWillReceiveProps(nextProps, nextState) {
    if (nextProps.windowWidth > 992) {
      if (!this.state.drawerOpen) {
        this.setState({
          drawerOpen: true
        });
      }
    }
    if (nextProps.windowWidth < 992) {
      if (this.state.drawerOpen) {
        this.setState({
          drawerOpen: false
        });
      }
    }
  }

  render() {
    return (
      <div>
        {this.renderUI()}
      </div>
    );
  }

  renderUI = () => {
    return (
      <ProfileUI
        states={this.state}
        windowWidth={this.props.windowWidth}
        handlers={this.handlers}
        headItems={this.headItems}
        minutes={this.minutes}
        messageFetchLimit={this.messageFetchLimit}
        serverUserInformation={this.serverUserInformation}
      />
    );
  };

  /**
     * @description : Resets the component and fetch data from the server
    *
    * @author Armin Eslami
    */
  load() {
    // Reset components state
    this.setState(this.DEFAULT_STATES);

    this.fetchUserProfile();
  }

  /**
     * @description : Handler for tapping on appbar's refresh button
    *
    * @author Armin Eslami
    */
  handleRefreshTouchTap = () => {
    this.load();
  };

  handleLogout = () => {
    cookie.remove('_wr', { path: '/' });
    this.props.dispatch(logout());
  };
  /**
  * @description : Handler for tapping on appbar's menu button
  *
  * @author Soorena Sanagoo
**/
  handleMenuButtonClick = () => {
    this.setState({
      drawerOpen: true
    });
  };
  /**
  * @description : Handler for toggle drawer
  *
  * @author Soorena Sanagoo
**/
  handleDrawerRequestChange = open => {
    this.setState({
      drawerOpen: open
    });
  };
  /**
  * @description : Handler for set activeStep
  *
  * @author Soorena Sanagoo
**/
  handleMenuClick = step => {
    if (this.props.type === 2) {
      this.headItems = {
        row: userProfileStrings.row,
        name: userProfileStrings.course.name,
        dateime: userProfileStrings.course.datetime
      };
    } else {
      this.headItems = {
        row: userProfileStrings.row,
        name: userProfileStrings.educationalGroup.name
      };
    }
    if (!this.state.isUpdating) {
      // disable drawer items when user updating information
      this.setState({
        mainViewClassName: '',
        activeStep: step,
        isUpdating: false
      });
    }
  };
  /**
  * @description : Handler for tapping on appbar's back button
  *
  * @author Soorena Sanagoo
**/
  handleBackTouchTap = () => {
    // TODO: change hashHistory to browserHistory on production !
    this.setState({
      mainViewClassName: 'top-to-bottom-move-out',
      appbarClassName: 'bottom-to-top-move-out',
      fabClassName: 'left-to-right-move-out',
      drawerClassName: 'left-to-right-move-out'
    });
    setTimeout(
      () => hashHistory.replace('/'), // TODO: change hashHistory to browserHistory on production !
      1000
    );
  };
  /**
  * @description : Handler for close snackbar
  *
  * @author Soorena Sanagoo
**/
  handleCloseSnackbar = () => {
    this.setState({
      isSnackOpen: false,
      snackMessage: ''
    });
  };
  /**
  * @description : compater two object
  *
  * @author Soorena Sanagoo

  * @param state:object

  * @return  true if objects are same and false if objects are different
**/

  compareObject = state => {
    var objectOne;
    var objectTwo;
    if (state.activeStep === 1) {
      objectOne = state.user.info;
      objectTwo = this.serverUserInformation.info;
      if (compareObject(objectOne, objectTwo)) {
        if (this.state.buttonStyle !== '') {
          this.setState({
            buttonStyle: ''
          });
        }
        return true;
      } else {
        if (this.state.buttonStyle !== 'pulse') {
          this.setState({
            buttonStyle: 'pulse'
          });
        }
        return false;
      }
    } else if (state.activeStep === 2) {
      objectOne = state.user.privacy;
      objectTwo = this.serverUserInformation.privacy;
      if (compareObject(objectOne, objectTwo)) {
        if (this.state.buttonStyle !== '') {
          this.setState({
            buttonStyle: ''
          });
        }
        return true;
      } else {
        if (this.state.buttonStyle !== 'pulse') {
          this.setState({
            buttonStyle: 'pulse'
          });
        }
        return false;
      }
    } else if (state.activeStep === 3) {
      objectOne = state.user.settings;
      objectTwo = this.serverUserInformation.settings;
      if (compareObject(objectOne, objectTwo)) {
        if (this.state.buttonStyle !== '') {
          this.setState({
            buttonStyle: ''
          });
        }
        return true;
      } else {
        if (this.state.buttonStyle !== 'pulse') {
          this.setState({
            buttonStyle: 'pulse'
          });
        }
        return false;
      }
    } else if (state.activeStep === 4) {
      if (this.props.type === 2) {
        objectOne = state.user.selectedCourses;
        objectTwo = this.serverUserInformation.selectedCourses;
      }
      if (this.props.type === 1) {
        objectOne = state.user.selectEducationalGroup;
        objectTwo = this.serverUserInformation.selectEducationalGroup;
      }
      if (objectOne === objectTwo) {
        return true;
      } else {
        return false;
      }
    }
  };
  ////////////////////////////////////////////////
  ////// ****   verify section      *****   //////
  ///////////////////////////////////////////////
  verifyEnteringYear = () => {
    if (this.state.user.info.year.length !== ENTERING_YEAR_LENGTH) {
      this.setState({
        errorText: {
          ...this.errorText,
          enteringYear: userProfileStrings.wrongEnteringYear
        }
      });
      return false;
    }
    return true;
  };
  verifyFirstName = () => {
    if (this.state.user.info.firstName === '') {
      this.setState({
        errorText: {
          ...this.errorText,
          firstName: userProfileStrings.errorText
        }
      });
      return false;
    }
    if (!isAlphabet(this.state.user.info.firstName)) {
      this.setState({
        errorText: {
          ...this.state.errorText,
          firstName: userProfileStrings.firstNameError
        }
      });
      return false;
    }

    if (
      this.state.user.info.firstName.length < MIN_FIRSTNAME_LENGTH &&
      this.state.user.info.firstName.length !== ''
    ) {
      this.setState({
        errorText: {
          ...this.state.errorText,
          firstName: userProfileStrings.firstNameError
        }
      });
      return false;
    }
    return true;
  };

  verifyLastName = () => {
    if (this.state.user.info.lastName === '') {
      this.setState({
        errorText: {
          ...this.state.errorText,
          lastName: userProfileStrings.errorText
        }
      });
      return false;
    }

    if (!isAlphabet(this.state.user.info.lastName)) {
      this.setState({
        errorText: {
          ...this.state.errorText,
          lastName: userProfileStrings.lastNameError
        }
      });
      return false;
    }

    if (
      this.state.user.info.lastName.length < MIN_LASTNAME_LENGTH &&
      this.state.user.info.lastName.length !== ''
    ) {
      this.setState({
        errorText: {
          ...this.state.errorText,
          lastName: userProfileStrings.lastNameError
        }
      });
      return false;
    }
    this.setState({ isError: false });
    return true;
  };

  verifyNewPassword = () => {
    if (
      this.state.user.privacy.newPassword !== undefined &&
      this.state.user.privacy.newPassword.length > 0 &&
      this.state.user.privacy.newPassword.length < PASSWORD_MIN_LENGTH
    ) {
      this.setState({
        errorText: {
          ...this.state.errorText,
          newPassword: userProfileStrings.newPasswordError
        }
      });
      return false;
    }
    if (
      this.state.user.privacy.confirmNewPassword !==
      this.state.user.privacy.newPassword
    ) {
      this.setState({
        errorText: {
          ...this.state.errorText,
          confirmNewPassword: userProfileStrings.confirmNewPasswordError
        }
      });
      return false;
    }
    this.setState({
      errorText: {
        ...this.state.errorText,
        confirmNewPassword: '',
        newPassword: ''
      }
    });
    return true;
  };
  verifyPhoneNumber = () => {
    if (this.state.user.info.mobile.length === 0) {
      return true;
    } else if (validateMobile(this.state.user.info.mobile)) {
      return true;
    } else {
      this.setState({
        errorText: {
          ...this.state.errorText,
          mobile: userProfileStrings.phoneNumberError
        }
      });
      return false;
    }
  };

  verifyEmail = () => {
    if (this.state.user.privacy.email.length === 0) {
      return true;
    } else {
      if (validateEmail(this.state.user.privacy.email)) {
        return true;
      } else {
        this.setState({
          errorText: {
            ...this.state.errorText,
            email: userProfileStrings.emailError
          }
        });
      }
    }
  };

  ////////////////////////////////////////////////
  // ****   personalinformation section  ****   //
  ///////////////////////////////////////////////

  /**
    * @description : for get textfields input from user profile ui component
    *
    * @author Soorena Sanagoo
    *
    * @param key( string):object key of state.user object
    *
    *.@param data (string):input data of textfields
    *
    * @return 
    */
  handleEnterPersonalData = (key, data) => {
    this.setState({
      user: {
        ...this.state.user,
        info: { ...this.state.user.info, [key]: data }
      }
    });
  };
  /**
  * @description : handler for remove avatar
  *
  * @author Soorena Sanagoo
**/
  handleRemoveAvatar = () => {
    this.setState({
      user: {
        ...this.state.user,
        avatarFile: {},
        info: { ...this.state.user.info, avatar: '' }
      }
    });
  };
  /**
  * @description : handler for set user's avatar
  *
  * @author Soorena Sanagoo

  * @param e(object):input file event arguments
**/
  handleAvatarChange = e => {
    if (e.target.files[0]) {
      if (
        process.env.REACT_APP_MAX_AVATAR_UPLOAD_SIZE_IN_BYTE >
        e.target.files[0].size
      ) {
        if (
          e.target.files[0].name.split('.').pop() === 'jpg' ||
          e.target.files[0].name.split('.').pop() === 'png' ||
          e.target.files[0].name.split('.').pop() === 'jpeg'
        ) {
          this.setState({
            user: {
              ...this.state.user,
              avatarFile: e.target.files[0],
              info: {
                ...this.state.user.info,
                avatar: URL.createObjectURL(e.target.files[0])
              }
            }
          });
        } else {
          this.setState({
            isSnackOpen: true,
            snackMessage: userProfileStrings.invalidFileType,
            user: {
              ...this.state.user,
              avatarFile: {},
              avatar: ''
            }
          });
        }
      } else {
        this.setState({
          isSnackOpen: true,
          snackMessage: userProfileStrings.fileSizeError.replace(
            'x',
            process.env.REACT_APP_MAX_AVATAR_UPLOAD_SIZE_IN_KBYTE.toString()
          ),
          user: {
            ...this.state.user,
            avatarFile: {},
            avatar: ''
          }
        });
      }
    } else {
    }
  };
  /**
  * @description : check user's info section and if it is change and form data is corrent call api
  *
  * @author Soorena Sanagoo
**/
  handleUpdateUserInfo = () => {
    if (!this.compareObject(this.state)) {
      if (
        this.verifyEnteringYear() &&
        this.verifyFirstName() &&
        this.verifyLastName() &&
        this.verifyPhoneNumber() &&
        this.state.user.info.gender !== ''
      ) {
        var userInfo = {
          ...this.state.user.info,
          avatarFile: this.state.user.avatarFile
        };
        this.setState({ isUpdating: true });
        this.updateUserInfo(userInfo);
      } else {
        this.setState({
          isSnackOpen: true,
          snackMessage: userProfileStrings.wrongData
        });
      }
    } else {
      this.setState({
        isSnackOpen: true,
        snackMessage: userProfileStrings.noChangesDetected
      });
    }
  };

  ////////////////////////////////////////////////
  // ****         privacy section        ****   //
  ///////////////////////////////////////////////
  handleEnterPrivacyData = (key, data) => {
    if (key !== 'email') {
      if (this.serverUserInformation.privacy[key] === undefined) {
        this.serverUserInformation = {
          ...this.serverUserInformation,
          privacy: {
            ...this.serverUserInformation.privacy,
            [key]: ''
          }
        };
      }
    }
    if (key !== 'password') {
      this.setState({
        user: {
          ...this.state.user,
          privacy: { ...this.state.user.privacy, [key]: data }
        }
      });
    } else {
      this.setState({
        user: {
          ...this.state.user,
          [key]: data
        }
      });
    }
  };
  /**
  * @description : check user's privacy section and if it is change and form data is corrent call api
  *
  * @author Soorena Sanagoo
**/
  handleUpdateUserPrivacy = () => {
    if (!this.compareObject(this.state)) {
      if (this.verifyEmail() && this.verifyNewPassword()) {
        this.setState({ isDialogOpen: true });
      } else {
        this.setState({
          isSnackOpen: true,
          snackMessage: userProfileStrings.wrongData
        });
      }
    } else {
      this.setState({
        isSnackOpen: true,
        snackMessage: userProfileStrings.noChangesDetected
      });
    }
  };
  handleConfirmUserPrivacy = () => {
    if (this.state.user.password.length > 0) {
      this.setState({ isUpdating: true });
      var privacy = {
        ...this.state.user.privacy,
        password: this.state.user.password
      };
      this.updateUserPrivacy(privacy);
    } else {
      this.setState({
        isSnackOpen: true,
        snackMessage: userProfileStrings.enterYourPassword
      });
    }
  };
  handleCloseDialog = () => {
    this.setState({
      isDialogOpen: false,
      user: {
        ...this.state.user,
        password: ''
      }
    });
  };

  ////////////////////////////////////////////////
  // ****         setting section        ****  //
  ///////////////////////////////////////////////
  handleEnterSettingsData = (key, data) => {
    if (key === 'timeForAutomaticallyLogout') {
      data = data * 60000;
    }
    this.setState({
      user: {
        ...this.state.user,
        settings: { ...this.state.user.settings, [key]: data }
      }
    });
  };
  /**
  * @description : check user's settings section and if it is change call api
  *
  * @author Soorena Sanagoo
**/
  handleUpdateUserSettings = () => {
    if (!this.compareObject(this.state)) {
      this.setState({ isUpdating: true });
      this.updateUserSettings(this.state.user.settings);
    } else {
      this.setState({
        isSnackOpen: true,
        snackMessage: userProfileStrings.noChangesDetected
      });
    }
  };

  ////////////////////////////////////////////////
  // ****         step 4 section        ****    //
  ///////////////////////////////////////////////

  handleClearSearchInput = () => {
    this.setState({
      searchText: '',
      showSearchResult: false
    });
  };
  /**
  * @description : handler for check search input and call  api search method
  *
  * @author Soorena Sanagoo
**/
  handlePerfomSearch = event => {
    this.setState({
      searchText: event.target.value
    });
    if (event.target.value.length >= MIN_SEARCH_LENGTH) {
      this.setState({
        isSearching: true
      });
      this.searchData(
        this.state.searchText,
        this.props.type === 2 ? 'course' : 'educationalGroup'
      );
    } else {
      this.setState({
        showSearchResult: false
      });
    }
  };
  /**
  * @description : handler for add item in users information(course or group)
  *
  * @author Soorena Sanagoo
**/
  handleAddItem = item => {
    var itemExist = undefined;

    if (this.props.type === 2) {
      itemExist = this.state.user.selectedCourses.filter(course => {
        return course.id === item.id;
      });
      if (itemExist.length === 0) {
        this.setState({
          user: {
            ...this.state.user,
            selectedCourses: [...this.state.user.selectedCourses, item]
          },
          searchData: [],
          searchText: '',
          showSearchResult: false
        });
      } else {
        this.setState({
          searchData: [],
          searchText: '',
          showSearchResult: false
        });
      }
    } else if (this.props.type === 1) {
      itemExist = this.state.user.selectEducationalGroup.filter(group => {
        return group.id === item.id;
      });
      if (itemExist.length === 0) {
        this.setState({
          user: {
            ...this.state.user,
            selectEducationalGroup: [
              ...this.state.user.selectEducationalGroup,
              item
            ]
          },
          searchData: [],
          searchText: '',
          showSearchResult: false
        });
      } else {
        this.setState({
          searchData: [],
          searchText: '',
          showSearchResult: false
        });
      }
    }
  };
  /**
  * @description : handle table checkbox to add item's id to a list (for removing)
  *
  * @author Soorena Sanagoo
**/
  handleClickTableCheckBox = item => {
    if (item.isChecked === true) {
      this.setState({
        idsToBeRemoved: [...this.state.idsToBeRemoved, item.id]
      });
    } else {
      var updated = this.state.idsToBeRemoved.filter(id => {
        return id !== item.id;
      });
      this.setState({
        idsToBeRemoved: updated
      });
    }
  };
  /**
  * @description : handle table header checkbox to add  all item's id to a list (for removing)
  *
  * @author Soorena Sanagoo
**/
  handleClickTableHeaderCheckBox = selectAll => {
    var deletedIds = [];

    if (this.props.type === 2) {
      deletedIds = [];
      if (selectAll) {
        for (var i = 0; i < this.state.user.selectedCourses.length; i++) {
          deletedIds.push(this.state.user.selectedCourses[i].id);
        }
        this.setState({
          idsToBeRemoved: deletedIds
        });
      } else {
        this.setState({
          idsToBeRemoved: []
        });
      }
    } else if (this.props.type === 1) {
      deletedIds = [];
      if (selectAll) {
        for (i = 0; i < this.state.user.selectEducationalGroup.length; i++) {
          deletedIds.push(this.state.user.selectEducationalGroup[i].id);
        }
        this.setState({
          idsToBeRemoved: deletedIds
        });
      } else {
        this.setState({
          idsToBeRemoved: []
        });
      }
    }
  };
  /**
  * @description : handler for Remove item in users information(course or group)
  *
  * @author Soorena Sanagoo
**/
  handleRemoveItem = () => {
    var i = 0;

    if (this.props.type === 2) {
      if (
        this.state.user.selectedCourses.length ===
        this.state.idsToBeRemoved.length
      ) {
        this.setState({
          idsToBeRemoved: [],
          user: {
            ...this.state.user,
            selectedCourses: []
          }
        });
      } else {
        var updatedCourse = this.state.user.selectedCourses;
        for (i = 0; i < this.state.idsToBeRemoved.length; i++) {
          updatedCourse = updatedCourse.filter(item => {
            return item.id !== this.state.idsToBeRemoved[i];
          });
        }
        this.setState({
          idsToBeRemoved: [],
          user: {
            ...this.state.user,
            selectedCourses: updatedCourse
          }
        });
      }
    } else if (this.props.type === 1) {
      if (
        this.state.user.selectEducationalGroup.length ===
        this.state.idsToBeRemoved.length
      ) {
        this.setState({
          idsToBeRemoved: [],
          user: {
            ...this.state.user,
            selectEducationalGroup: []
          }
        });
      } else {
        var updatedGroups = this.state.user.selectEducationalGroup;
        for (i = 0; i < this.state.idsToBeRemoved.length; i++) {
          updatedGroups = updatedGroups.filter(item => {
            return item.id !== this.state.idsToBeRemoved[i];
          });
        }
        this.setState({
          idsToBeRemoved: [],
          user: {
            ...this.state.user,
            selectEducationalGroup: updatedGroups
          }
        });
      }
    }
  };
  /**
  * @description : check user's course list and if it is change and form data is corrent call api
  *
  * @author Soorena Sanagoo
**/
  handleUpdateCourseList = () => {
    if (!this.compareObject(this.state)) {
      this.setState({ isUpdating: true });
      this.updateUserCourses(this.state.user.selectedCourses);
    } else {
      this.setState({
        isSnackOpen: true,
        snackMessage: userProfileStrings.noChangesDetected
      });
    }
  };
  /**
  * @description : check user's group list and if it is change and form data is corrent call api
  *
  * @author Soorena Sanagoo
**/
  handleUpdateEducationalGroupList = () => {
    if (!this.compareObject(this.state)) {
      this.setState({ isUpdating: true });
      this.updateUserEducationalGroups(this.state.user.selectEducationalGroup);
    } else {
      this.setState({
        isSnackOpen: true,
        snackMessage: userProfileStrings.noChangesDetected
      });
    }
  };

  /////////////////////////////////////////////////
  //               api call methods              //
  /////////////////////////////////////////////////
  fetchUserProfile = () => {
    fetchUserProfile(this.props.token, this.props.userType).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('fetchUserProfile() ended with result', res)
          : void 0;
        if (res.code === 200 && res.data) {
          this.props.dispatch(
            updateUser({
              token: res.data.token ? res.data.token : null
            })
          );

          // Update states
          this.serverUserInformation = res.data.user;
          this.setState({
            isLoading: false,
            isFetchError: false,
            user: {
              ...this.state.user,
              ...res.data.user
            }
          });
        } else if (res.code === 400) {
          this.setState({
            isLoading: false,
            isFetchError: true
          });
        } else {
          this.setState({
            isLoading: false,
            isFetchError: true
          });
        }
      },
      err => {
        this.setState({
          isLoading: false,
          isFetchError: true
        });
      }
    );
  };

  updateUserInfo = data => {
    updateUserInfo(this.props.token, data).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('updateUserInfo() ended with result', res)
          : void 0;
        if (res.code === 200 && res.data) {
          this.props.dispatch(
            updateUser({
              token: res.data.token ? res.data.token : null,
              firstName: res.data.info.firstName,
              lastName: res.data.info.lastName,
              avatar:
                res.data.info.avatar.length > 0
                  ? process.env.REACT_APP_DOMAIN +
                    process.env.REACT_APP_IMAGES_DIRECTORY +
                    res.data.user.avatar
                  : null
            })
          );
          this.serverUserInformation = {
            ...this.serverUserInformation,
            info: res.data.info
          };
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: userProfileStrings.applyChanges,
            user: {
              ...this.state.user,
              info: res.data.info
            }
          });
        } else if (res.code === 400) {
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: res.errors[0].message
          });
        } else {
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: GlobalStrings.fatalError
          });
        }
      },
      err => {
        this.setState({
          isUpdating: false,
          isSnackOpen: true,
          snackMessage: GlobalStrings.fatalError
        });
      }
    );
  };
  updateUserPrivacy = data => {
    updateUserPrivacy(this.props.token, data).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('updateUserPrivacy() ended with result', res)
          : void 0;
        if (res.code === 200 && res.data) {
          this.props.dispatch(
            updateUser({
              token: res.data.token ? res.data.token : null
            })
          );
          this.serverUserInformation = {
            ...this.serverUserInformation,
            privacy: res.data.privacy
          };
          this.setState({
            isUpdating: false,
            isDialogOpen: false,
            isSnackOpen: true,
            snackMessage: userProfileStrings.applyChanges,
            user: {
              ...this.state.user,
              password: '',
              privacy: res.data.privacy
            }
          });
        } else if (res.code === 400) {
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: res.errors[0].message
          });
        } else {
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: GlobalStrings.fatalError
          });
        }
      },
      err => {
        this.setState({
          isUpdating: false,
          isSnackOpen: true,
          snackMessage: GlobalStrings.fatalError
        });
      }
    );
  };
  updateUserSettings = data => {
    updateUserSettings(this.props.token, data).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('updateUserSettings() ended with result', res)
          : void 0;
        if (res.code === 200 && res.data) {
          this.props.dispatch(
            updateUser({
              token: res.data.token ? res.data.token : null,
              settings: {
                messageFetchLimit: res.data.settings.messageFetchLimit,
                idleTime: res.data.settings.timeForAutomaticallyLogout,
                combinedFetch: res.data.settings.showAllMessageFromAllAccount
              }
            })
          );
          this.serverUserInformation = {
            ...this.serverUserInformation,
            settings: res.data.settings
          };
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: userProfileStrings.applyChanges,
            user: {
              ...this.state.user,
              settings: res.data.settings
            }
          });
        } else if (res.code === 400) {
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: res.errors[0].message
          });
        } else {
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: GlobalStrings.fatalError
          });
        }
      },
      err => {
        this.setState({
          isUpdating: false,
          isSnackOpen: true,
          snackMessage: GlobalStrings.fatalError
        });
      }
    );
  };
  updateUserCourses = data => {
    updateUserCourses(this.props.token, data).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('updateUserCourses() ended with result', res)
          : void 0;
        if (res.code === 200 && res.data) {
          this.props.dispatch(
            updateUser({
              token: res.data.token ? res.data.token : null
            })
          );
          this.serverUserInformation = {
            ...this.serverUserInformation,
            selectedCourses: res.data.selectedCourses
          };
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: userProfileStrings.applyChanges,
            user: {
              ...this.state.user,
              selectedCourses: res.data.selectedCourses
            }
          });
        } else if (res.code === 400) {
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: res.errors[0].message
          });
        } else {
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: GlobalStrings.fatalError
          });
        }
      },
      err => {
        this.setState({
          isUpdating: false,
          isSnackOpen: true,
          snackMessage: GlobalStrings.fatalError
        });
      }
    );
  };
  updateUserEducationalGroups = data => {
    updateUserEducationalGroups(this.props.token, data).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('updateUserEducationalGroups() ended with result', res)
          : void 0;
        if (res.code === 200 && res.data) {
          this.props.dispatch(
            updateUser({
              token: res.data.token ? res.data.token : null
            })
          );
          this.serverUserInformation = {
            ...this.serverUserInformation,
            selectEducationalGroup: res.data.selectEducationalGroup
          };
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: userProfileStrings.applyChanges,
            user: {
              ...this.state.user,
              selectEducationalGroup: res.data.selectEducationalGroup
            }
          });
        } else if (res.code === 400) {
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: res.errors[0].message
          });
        } else {
          this.setState({
            isUpdating: false,
            isSnackOpen: true,
            snackMessage: GlobalStrings.fatalError
          });
        }
      },
      err => {
        this.setState({
          isUpdating: false,
          isSnackOpen: true,
          snackMessage: GlobalStrings.fatalError
        });
      }
    );
  };

  searchData = (searchText, dataType) => {
    let formData = new FormData();
    formData.append('searchText', searchText);
    formData.append('dataType', dataType);
    search(this.props.token, formData).then(
      res => {
        process.env.NODE_ENV === 'development'
          ? console.log('searchData() ended with result', res)
          : void 0;
        if (res.code === 200) {
          this.setState({
            isSearching: false,
            searchResult: res.data.searchResult,
            showSearchResult: true
          });
        } else if (res.code === 400) {
          this.setState({
            isSearching: false,
            showSearchResult: false,
            searchText: '',
            isSnackOpen: true,
            snackMessage: res.errors[0].message
          });
        } else {
          this.setState({
            isSearching: false,
            showSearchResult: false,
            searchText: '',
            isSnackOpen: true,
            snackMessage: GlobalStrings.fatalError
          });
        }
      },
      err => {
        this.setState({
          isSearching: false,
          showSearchResult: false,
          isSnackOpen: true,
          snackMessage: GlobalStrings.fatalError
        });
      }
    );
  };
} // End of the class

export default connect(state => {
  return {
    token: state.user.token,
    windowWidth: state.app.windowWidth,
    type: state.user.type
  };
})(Profile);



