import React from 'react';
import { Card } from 'material-ui/Card';
import { Row, Col } from 'react-flexbox-grid-aphrodite';
import RaisedButton from 'material-ui/RaisedButton';
import CircularProgress from 'material-ui/CircularProgress';
import DropDownMenu from 'material-ui/DropDownMenu';
import Toggle from 'material-ui/Toggle';
import Divider from 'material-ui/Divider';

import BaseComponent from 'components/base/BaseComponent';
import { Profile as userProfileStrings } from 'i18n/Persian';
export class Setting extends BaseComponent {
  render() {
    return (
      <div>
        {this.renderUI()}
      </div>
    );
  }
  renderUI = () => {
    var buttonOrCircular;

    if (this.props.states.isUpdating) {
      buttonOrCircular = <CircularProgress size={25} />;
    } else {
      buttonOrCircular = (
        <RaisedButton
          label={userProfileStrings.save}
          className={this.props.states.buttonStyle}
          primary={true}
          onTouchTap={event => {
            this.props.handlers.onUpdateUserSettings();
          }}
        />
      );
    }
    return (
      <div
        className={
          'childComponent container ' + this.props.states.mainViewClassName
        }
      >
        <Row center="xs">
          <Col lg={5} md={8} sm={8} xs={12}>
            <Card>
              <div style={{ textAlign: 'right', padding: 16 }}>
                <div style={{ display: 'inline-block' }}>
                  <span style={{ fontFamily: 'iransans', fontSize: '1.0rem' }}>
                    {userProfileStrings.messageFetchLimit}
                  </span>
                </div>
                <div style={{ display: 'inline-block' }}>
                  <DropDownMenu
                    value={this.props.states.user.settings.messageFetchLimit}
                    maxHeight={200}
                    style={{ marginTop: '-46px', marginLeft: '-12px', top: 24 }}
                    underlineStyle={{ display: 'none' }}
                    menuItemStyle={{ textAlign: 'left' }}
                    onChange={(event, index, value) => {
                      this.props.handlers.onEnterSettingsData(
                        'messageFetchLimit',
                        value
                      );
                    }}
                  >
                    {this.props.messageFetchLimit}
                  </DropDownMenu>
                </div>

                <div>
                  <p
                    className="menuDescription"
                    style={{ paddingRight: 0, margin: 0 }}
                  >
                    {userProfileStrings.messageFetchLimitDescription}
                  </p>
                </div>
              </div>
              <Divider />
              <div style={{ textAlign: 'right', padding: 16 }}>
                <div style={{ display: 'inline-block' }}>
                  <Toggle
                    label={userProfileStrings.fetchMessageFromAllAccounts}
                    labelPosition="left"
                    toggled={
                      this.props.states.user.settings
                        .showAllMessageFromAllAccount
                    }
                    labelStyle={{ fontFamily: 'iransans', fontSize: '1.0rem' }}
                    onToggle={(event, bool) => {
                      this.props.handlers.onEnterSettingsData(
                        'showAllMessageFromAllAccount',
                        bool
                      );
                    }}
                  />
                </div>
                <div style={{ display: 'inline-block' }}>
                  <p
                    className="menuDescription"
                    style={{ paddingRight: 0, marginTop: 0 }}
                  >
                    {userProfileStrings.fetchMessageFromAllAccountsDescription}
                  </p>
                </div>
              </div>
              <Divider />
              <div style={{ textAlign: 'right', padding: 16 }}>
                <div style={{ display: 'inline-block' }}>
                  <span style={{ fontFamily: 'iransans', fontSize: '1.0rem' }}>
                    {userProfileStrings.timeForLogOutAutomatically}
                  </span>
                </div>
                <div style={{ display: 'inline-block' }}>
                  <DropDownMenu
                    value={
                      this.props.states.user.settings
                        .timeForAutomaticallyLogout / 60000
                    }
                    maxHeight={200}
                    style={{ marginTop: '-46px', marginLeft: '-12px', top: 25 }}
                    underlineStyle={{ display: 'none' }}
                    menuItemStyle={{ textAlign: 'left' }}
                    onChange={(event, index, value) => {
                      this.props.handlers.onEnterSettingsData(
                        'timeForAutomaticallyLogout',
                        value
                      );
                    }}
                  >
                    {this.props.minutes}
                  </DropDownMenu>
                </div>
                <p
                  className="menuDescription"
                  style={{ marginTop: 0, paddingRight: 0 }}
                >
                  {userProfileStrings.timeForLogOutAutomaticallyDescription}
                </p>
              </div>
              <Row end="xs">
                <Col xs={12} style={{ padding: 16 }}>
                  {buttonOrCircular}
                </Col>
              </Row>
            </Card>
          </Col>
        </Row>
      </div>
    );
  };
}
export default Setting;



