/**
* @description : This components renders an table using Material-Ui table components
*
* @prop headItems (object) : An object which each key's value is the header title
* @prop items (array) : An array of items to be rendered on table body
* @prop paginationLimit (int) : The number of rows on each pages of table
* @prop paginationLimitSteps (array) : An array of pagination steps
* @prop onTableRowClick (callback) : A callback which is fired when a table row is clicked
* @prop hasFooter (boolean) : True if table needs a footer with pagiantion options
* @prop hasSearch (boolean) : True if table needs a search input
* @prop showCheckbox (boolen) : On true shows checkboxes on table
* @prop onTableHeaderCheckboxClick (callback) : Callback for the click event on table header checkbox
* @prop onTableBodyCheckboxClick : (callback) for the click event on table rows checkbox
* @prop onDelete (callback) : Fires when the delete button is clicked
* @prop searchFieldClassName (string) : A class name for search container
* @prop tableClassName (string) : A class name for table
* @prop paginationDropdownClassName (string) : A class name for pagination steps dropdown
* @prop controllsClassName (string) : A class name for controlls
* @prop excludedColumnsOnSmallScreen (array) : An array of columns key name to be excluded on small screen size
* @prop compressPoint (number) : The minimum width of the screen in pixel which will make the table to compress itself
*       if the screen size is less than @prop compressPoint
* @prop searchFieldHint: Text to show as search field hint
* @prop noSearchResultText: Text to show when search returns no result
*
* @author Armin Eslami
* @version 1.0.0
*/

import React from 'react';
import PropTypes from 'prop-types';
import {
  Table as MaterialTable,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow as TR,
  TableRowColumn
} from 'material-ui/Table';
import { TableFooter, IconButton } from 'material-ui';
import DropDownMenu from 'material-ui/DropDownMenu';
import MenuItem from 'material-ui/MenuItem';
import CheveronRight from 'material-ui/svg-icons/navigation/chevron-right';
import CheveronLeft from 'material-ui/svg-icons/navigation/chevron-left';
import Checkbox from 'material-ui/Checkbox';
import ActionDelete from 'material-ui/svg-icons/action/delete';
import ActionSearch from 'material-ui/svg-icons/action/search';
import NavigationClose from 'material-ui/svg-icons/navigation/close';

import TableRow from './TableRow';
import './Style.css';

const texts = {
  searchPlaceholder: 'جستجوی عنوان پیام',
  noSearchResult: 'نتیجه ای یافت نشد'
};

var totalItems;

export class Table extends React.Component {
  constructor(props) {
    super(props);
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    this.state = {
      styles: {
        searchContainer: {
          background: '#ffffff', //'rgb(245,245,245)',
          display: 'flex',
          width: ' 100%',
          textAlign: 'right',
          borderBottom: '1px solid rgb(224,224,224)'
        },

        controlls: {
          background: '#ffffff',
          display: 'none',
          width: '100%',
          height: '58px',
          borderBottom: '1px solid rgb(224,224,224)'
        },

        hover: {
          cursor: 'pointer'
        },

        search: {
          flex: '1',
          height: '58px',
          background: '#ffffff', //'rgb(245,245,245)',
          border: 'none',
          // borderBottom: '1px solid rgb(224,224,224)',
          padding: '0px 16px',
          fontSize: '.8rem'
        }
      },
      isTableCompressed: false
    };
  }

  /**
     * @description : This is called when the component is mounted. The goal is to add
     * an event listener for screen resize
     *
     * @author Armin Eslami
     * @return
     */
  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener('resize', this.updateWindowDimensions);
  }

  /**
     * @description : Removes screen resize listener
     *
     * @author Armin Eslami
     */
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateWindowDimensions);
  }

  /**
     * @description : This is the place to update the width and height on the state, when the screen is resizing
     *
     * @author Armin Eslami
     */
  updateWindowDimensions() {
    var isTableCompressed = window.innerWidth <= this.props.compressPoint;

    if (isTableCompressed !== this.state.isTableCompressed) {
      this.setState({ isTableCompressed });
    }
  }

  componentWillMount() {
    if (this.props.items) {
      var items = this.props.items;
      var temp = [];

      for (var i = 0; i < items.length; i++) {
        temp.push({ ...items[i], isChecked: false });
        temp[i].isChecked = false;
      }
    }

    this.setState({
      isAllRowChecked: false,
      checkAllCheckboxes: false,
      offset: this.props.offset || 0,
      paginationLimit: this.props.paginationLimit || 20,
      paginationLimitDropdownSelectedIndex: 1,
      searchText: '',
      items: temp || []
    });
  }

  componentWillReceiveProps(nextProps) {
    // If there are new items first save them
    // then add isChecked value for them which sould be equal to
    // last isChecked values form state.items
    if (nextProps.items) {
      var items = nextProps.items;
      var temp = [];
      var stateitems = this.state.items;

      if (items.length > stateitems.length) {
        this.setState({ checkAllCheckboxes: false });
      }

      for (var i = 0; i < items.length; i++) {
        temp.push({ ...items[i], isChecked: false });
        for (var j = 0; j < stateitems.length; j++) {
          if (temp[i].id === stateitems[j].id) {
            temp[i].isChecked = stateitems[j].isChecked;
            break;
          }
        }
      }
    }

    this.setState(
      {
        offset: nextProps.offset || this.state.offset,
        paginationLimit:
          nextProps.paginationLimit || this.state.paginationLimit,
        items: temp || this.state.items
      },
      () => this.toggleControllerComponent()
    );
  }

  /**
    * @description : Changes pagination limit by dispatching an action and updating related state
    *
    * @author Armin Eslami
    *
    * @param limit (int) : The limit for number of rows on per page of table
    * @param dropdownSelectedIndex : The index of selected value on pagination dropdown
    */
  handlePaginationLimitChange(limit, dropdownSelectedIndex) {
    this.setState({
      paginationLimit: limit,
      paginationLimitDropdownSelectedIndex: dropdownSelectedIndex
    });
    // this.props.dispatch(actions.changePaginationLimit(limit));
  }

  handleCheckAllClick() {
    var items = this.state.items;
    for (var i = 0; i < items.length; i++) {
      items[i].isChecked = this.state.checkAllCheckboxes;
    }

    this.setState({ items: items }, () => {
      this.toggleControllerComponent();
    });

    this.props.onTableHeaderCheckboxClick(this.state.checkAllCheckboxes);
  }

  handleTableBodyCheckboxClick(obj) {
    var items = this.state.items;

    for (var i = 0; i < items.length; i++) {
      if (items[i].id === obj.id) {
        items[i].isChecked = !items[i].isChecked;
        if (!items[i].isChecked) {
          //NOTE : do i need to call this.props.onTableHeaderCheckboxClick(this.state.checkAllCheckboxes) after state is set ?!
          this.setState({ checkAllCheckboxes: false });
        }
        break;
      }
    }

    this.setState({ items: items }, () => {
      this.toggleControllerComponent();
    });

    this.props.onTableBodyCheckboxClick(obj);
  }

  toggleControllerComponent = () => {
    var items = this.state.items;

    for (var i = 0; i < items.length; i++) {
      if (items[i].isChecked) {
        this.setState({
          styles: {
            ...this.state.styles,
            searchContainer: {
              ...this.state.styles.searchContainer,
              display: 'none'
            },

            controlls: {
              ...this.state.styles.controlls,
              display: 'flex'
            }
          }
        });
        return;
      }
    }

    this.setState({
      styles: {
        ...this.state.styles,
        searchContainer: {
          ...this.state.styles.searchContainer,
          display: 'flex'
        },

        controlls: {
          ...this.state.styles.controlls,
          display: 'none'
        }
      }
    });
  };

  renderUI = () => {
    if (this.state.items.length > 0) {
      return this.renderTable();
    }
  };

  renderTable = () => {
    var items = this.state.items;
    var paginationLimit = this.state.paginationLimit;

    if (items && items.length > 0) {
      // To hold the final items
      var filtereditems = [];
      // To hold filtered items by user search input
      var filtereditemsBySearch = [];
      filtereditemsBySearch = items;
      // To make sure the number of rendered items on each page is equal to the pagination limit
      var numberOfRendereditems = 0;
      // To add each item row number
      // var rowNumber = 1;
      // To hold the number of items before doing any filter
      totalItems = items.length;

      // If the search text is present filter all the items based on it.
      filtereditemsBySearch = items.filter(item => {
        return (
          this.state.searchText.length === 0 ||
          item.title.indexOf(this.state.searchText) > -1
        );
      });

      // Now get the final items based on offset and pagination limit
      for (
        var i = this.state.offset;
        i < totalItems && numberOfRendereditems < paginationLimit;
        i++
      ) {
        if (this.state.searchText.length > 0) {
          if (filtereditemsBySearch[i]) {
            filtereditems[i] = filtereditemsBySearch[i];
          }
        } else {
          filtereditems[i] = items[i];
        }
        numberOfRendereditems++;
      }

      // If there is a search text, set totalItems equal to filtereditems length
      totalItems =
        this.state.searchText.length > 0 ? filtereditems.length : totalItems;

      return (
        <div>
          <div
            style={this.state.styles.controlls}
            className={this.props.controllsClassName || ''}
          >
            <div style={{ flex: 1, textAlign: 'right' }}>
              <ActionDelete
                id="controllsDelete"
                style={{
                  color: 'rgb(106,110,116)',
                  margin: '12px 24px 16px 16px',
                  cursor: 'pointer'
                }}
                onTouchTap={this.props.onDelete ? this.props.onDelete : void 0}
              />
            </div>
          </div>

          {this.props.hasSearch
            ? <div style={this.state.styles.searchContainer}>
                <IconButton
                  style={{ marginLeft: 12, marginTop: 6 }}
                  onTouchTap={e => {
                    if (this.state.searchText.length > 0) {
                      setTimeout(() => {
                        this.setState({ searchText: '' });
                        this.refs.itemsSearchField.value = '';
                      }, 150);
                    }
                  }}
                >
                  {this.state.searchText
                    ? <NavigationClose />
                    : <ActionSearch />}
                </IconButton>

                <input
                  style={{ ...this.props.style, ...this.state.styles.search }}
                  placeholder={this.props.searchFieldHint}
                  ref="itemsSearchField"
                  className={this.props.searchFieldClassName}
                  onChange={e => {
                    this.setState({
                      offset: 0,
                      searchText: this.refs.itemsSearchField.value
                    });
                  }}
                />
              </div>
            : void 0}

          <MaterialTable
            selectable={true}
            className={this.props.tableClassName}
          >
            <TableHeader adjustForCheckbox={false} displaySelectAll={false}>
              <TR>
                {this.renderTableHead()}
              </TR>
            </TableHeader>

            <TableBody displayRowCheckbox={false} ref="tableBody">
              {this.renderTableBody(filtereditems)}
            </TableBody>

            {this.renderTableFooter()}
          </MaterialTable>
        </div>
      );
    } // end of if
  };

  renderTableHead = () => {
    var headItems = [];

    if (this.props.showCheckbox) {
      headItems.push(
        <TableHeaderColumn key={0} style={{ width: 30 }}>
          <Checkbox
            labelStyle={{ display: 'none' }}
            onTouchTap={
              this.props.onTableHeaderCheckboxClick
                ? e => {
                    this.setState(
                      { checkAllCheckboxes: !this.state.checkAllCheckboxes },
                      () => {
                        this.handleCheckAllClick();
                      }
                    );
                  }
                : void 0
            }
            checked={this.state.checkAllCheckboxes}
          />
        </TableHeaderColumn>
      );
    }

    Object.keys(this.props.headItems).map(keyName => {
      if (
        !this.state.isTableCompressed ||
        (this.state.isTableCompressed &&
          this.props.excludedColumnsOnSmallScreen.indexOf(keyName) === -1)
      ) {
        headItems.push(
          <TableHeaderColumn key={keyName}>
            {this.props.headItems[keyName]}
          </TableHeaderColumn>
        );
      }
      return null;
    });
    return headItems;
  };

  renderTableBody = items => {
    if (this.state.searchText.length > 0 && totalItems === 0) {
      return (
        <TR>
          <TableRowColumn style={{ textAlign: 'center', marginTop: 16 }}>
            <ActionSearch style={{ marginTop: 16 }} />
            <p style={{ marginTop: 0 }}>
              {this.props.noSearchResultText}
            </p>
          </TableRowColumn>
        </TR>
      );
    } else {
      return items.map(item => {
        return (
          <TableRow
            key={item.id}
            item={{ ...item }}
            onTableRowClick={
              this.props.onTableRowClick
                ? id => {
                    this.props.onTableRowClick(id);
                  }
                : void 0
            }
            showCheckbox={this.props.showCheckbox}
            onTableBodyCheckboxClick={
              this.props.onTableBodyCheckboxClick
                ? obj => this.handleTableBodyCheckboxClick(obj)
                : void 0
            }
            isTableCompressed={false}
            excludedColumnsOnSmallScreen={
              this.state.isTableCompressed
                ? this.props.excludedColumnsOnSmallScreen
                : []
            }
          />
        );
      });
    }
  };

  renderTableFooter = () => {
    if (this.props.hasFooter !== false) {
      return (
        <TableFooter adjustForCheckbox={false}>
          <TR>
            <TableRowColumn style={{ float: 'right', padding: 0 }}>
              <IconButton
                disabled={this.state.offset === 0}
                onTouchTap={() => {
                  this.setState({
                    offset: parseInt(
                      this.state.offset - this.state.paginationLimit,
                      10
                    )
                  });
                }}
              >
                <CheveronRight />
              </IconButton>
              <IconButton
                disabled={
                  this.state.offset + this.state.paginationLimit >= totalItems
                }
                onTouchTap={() => {
                  this.setState({
                    offset: parseInt(
                      this.state.offset + this.state.paginationLimit,
                      10
                    )
                  });
                }}
              >
                <CheveronLeft />
              </IconButton>
            </TableRowColumn>
            <TableRowColumn
              style={{ float: 'right', paddingTop: 16, height: 16 }}
            >
              {Math.min(this.state.offset + 1, totalItems) +
                ' - ' +
                Math.min(
                  this.state.offset + this.state.paginationLimit,
                  totalItems
                ) +
                ' از ' +
                totalItems}
            </TableRowColumn>
            <TableRowColumn style={{ display: 'inline-block' }}>
              <DropDownMenu
                className={this.props.paginationDropdownClassName}
                iconStyle={{ paddingRight: 0 }}
                value={this.state.paginationLimitDropdownSelectedIndex}
                style={{ width: 96, bottom: 1 }}
                underlineStyle={{ display: 'none' }}
                onChange={(event, index, value) => {
                  this.handlePaginationLimitChange(
                    event.target.innerText,
                    value
                  );
                }}
              >
                {this.renderPaginationLimitSteps()}
              </DropDownMenu>
            </TableRowColumn>
          </TR>
        </TableFooter>
      );
    }
  };

  renderPaginationLimitSteps = () => {
    var propsSteps = this.props.paginationLimitSteps;
    var steps = [];
    var i = undefined;

    if (propsSteps && propsSteps.length > 0) {
      for (i = 0; i < propsSteps.length; i++) {
        steps.push(
          <MenuItem key={i + 1} value={i + 1} primaryText={propsSteps[i]} />
        );
      }
    } else {
      for (i = 0; i < 5; i++) {
        steps.push(
          <MenuItem key={i + 1} value={i + 1} primaryText={i + 1 + '0'} />
        );
      }
    }

    return steps;
  };

  render() {
    return (
      <div>
        {this.renderUI()}
      </div>
    );
  }
}

Table.defaultProps = {
  hasFooter: true,
  hasSearch: true,
  searchFieldHint: texts.searchPlaceholder,
  noSearchResultText: texts.noSearchResult,
  paginationLimitSteps: [10, 20, 30, 40, 50],
  excludedColumnsOnSmallScreen: [],
  compressPoint: 414
};

Table.propTypes = {
  headItems: PropTypes.object.isRequired,
  items: PropTypes.array.isRequired,
  paginationLimit: PropTypes.number,
  paginationLimitSteps: PropTypes.array,
  onTableRowClick: PropTypes.func,
  onTableHeaderCheckboxClick: PropTypes.func,
  onTableBodyCheckboxClick: PropTypes.func,
  onDelete: PropTypes.func,
  hasFooter: PropTypes.bool,
  hasSearch: PropTypes.bool,
  showCheckbox: PropTypes.bool,
  searchFieldClassName: PropTypes.string,
  tableClassName: PropTypes.string,
  paginationDropdownClassName: PropTypes.string,
  controllsClassName: PropTypes.string,
  excludedColumnsOnSmallScreen: PropTypes.array,
  compressPoint: PropTypes.number
};

export default Table;



