import React from 'react';
import { connect } from 'react-redux';
import Avatar from 'material-ui/Avatar';
import { Card } from 'material-ui/Card';
import { Row, Col } from 'react-flexbox-grid-aphrodite';
import IconButton from 'material-ui/IconButton';
import SvgIcon from 'material-ui/SvgIcon';
import TextField from 'material-ui/TextField';
import { RadioButton, RadioButtonGroup } from 'material-ui/RadioButton';
import RaisedButton from 'material-ui/RaisedButton';
import CircularProgress from 'material-ui/CircularProgress';
import DropDownMenu from 'material-ui/DropDownMenu';
import MenuItem from 'material-ui/MenuItem';

import BaseComponent from 'components/base/BaseComponent';
import { Profile as userProfileStrings } from 'i18n/Persian';
import { isNumber } from 'helpers/Helpers.js';
const Remove = props =>
  <SvgIcon {...props}>
    <path d="M19,4H15.5L14.5,3H9.5L8.5,4H5V6H19M6,19A2,2 0 0,0 8,21H16A2,2 0 0,0 18,19V7H6V19Z" />
  </SvgIcon>;
export class PersonalInformation extends BaseComponent {
  constructor(props) {
    super(props);
    this.state = {
      hover: false // to hold hover on avatar
    };
  }
  render() {
    return (
      <div>
        {this.renderUI()}
      </div>
    );
  }
  renderUI = () => {
    return (
      <Row
        className={
          'childComponent container ' + this.props.states.mainViewClassName
        }
        center="xs"
      >
        <Col lg={10} md={10} sm={10} xs={12}>
          <Card containerStyle={{ direction: 'rtl' }}>
            <Row>
              <Col
                lg={4}
                md={4}
                sm={4}
                xs={12}
                style={
                  this.props.windowWidth > 767
                    ? {
                        position: 'relative',
                        backgroundColor: 'whitesmoke',
                        right: '7.2px'
                      }
                    : { position: 'relative', right: 0 }
                }
              >
                {this.renderAvatarSection()}
              </Col>
              <Col lg={8} md={8} sm={8} xs={12}>
                {this.renderInformationSection()}
              </Col>
            </Row>
          </Card>
        </Col>
      </Row>
    );
  };

  renderInformationSection = () => {
    var buttonOrCircular;

    if (this.props.states.isUpdating) {
      buttonOrCircular = <CircularProgress size={25} />;
    } else {
      buttonOrCircular = (
        <RaisedButton
          label={userProfileStrings.save}
          className={this.props.states.buttonStyle}
          primary={true}
          onTouchTap={event => {
            this.props.handlers.onUpdateUserInfo();
          }}
        />
      );
    }
    return (
      <div style={{ padding: '16px' }}>
        <Row start="xs">
          <Col lg={4} md={4} sm={4} xs={12}>
            <TextField
              floatingLabelText={userProfileStrings.universityName}
              value={this.props.userInfo.universityName}
              disabled={true}
              style={{
                width: '100%',
                fontFamily: 'iransans',
                fontSize: '0.9rem'
              }}
            />
          </Col>
        </Row>
        <Row>
          <Col lg={4} md={4} sm={4} xs={12} style={{ padding: '8px' }}>
            <TextField
              floatingLabelText={userProfileStrings.firstName}
              errorText={this.props.errorText.firstName}
              value={this.props.userInfo.firstName}
              onChange={e => {
                this.props.errorText.firstName = '';
                this.props.handlers.onEnterPersonalData(
                  'firstName',
                  e.target.value
                );
              }}
              onBlur={() => this.props.handlers.verifyFirstName()}
              style={{
                width: '100%',
                fontFamily: 'iransans',
                fontSize: '0.9rem'
              }}
              errorStyle={{ float: 'left' }}
            />
          </Col>
          <Col lg={4} md={4} sm={4} xs={12} style={{ padding: '8px' }}>
            <TextField
              floatingLabelText={userProfileStrings.lastName}
              errorText={this.props.errorText.lastName}
              value={this.props.userInfo.lastName}
              onChange={e => {
                this.props.errorText.lastName = '';
                this.props.handlers.onEnterPersonalData(
                  'lastName',
                  e.target.value
                );
              }}
              onBlur={() => this.props.handlers.verifyLastName()}
              style={{
                width: '100%',
                fontFamily: 'iransans',
                fontSize: '0.9rem'
              }}
              errorStyle={{ float: 'left' }}
            />
          </Col>
        </Row>
        <Row start="xs">
          <Col lg={4} md={4} sm={4} xs={12} style={{ padding: '8px' }}>
            <TextField
              className="inputs"
              floatingLabelText={userProfileStrings.phoneNumber}
              errorText={this.props.errorText.mobile}
              onChange={e => {
                this.props.errorText.mobile = '';
                this.props.handlers.onEnterPersonalData(
                  'mobile',
                  e.target.value
                );
              }}
              onBlur={() => this.props.handlers.verifyPhoneNumber()}
              value={this.props.userInfo.mobile}
              style={{
                width: '100%',
                fontFamily: 'iransans',
                fontSize: '0.9rem'
              }}
            />
          </Col>
        </Row>
        <Row start="xs">
          <Col lg={6} md={7} sm={7} xs={12} style={{ padding: '8px' }}>
            <p
              style={{
                display: 'inline-block',
                fontFamily: 'iransans',
                fontSize: '0.9rem'
              }}
            >
              {userProfileStrings.gender}
            </p>
            <RadioButtonGroup
              name="employee_gender"
              style={{
                display: 'inline-block',
                top: '10px',
                position: 'relative'
              }}
              onChange={(e, v) => {
                this.props.handlers.onEnterPersonalData('gender', v);
              }}
              valueSelected={this.props.userInfo.gender}
            >
              <RadioButton
                value="male"
                label={userProfileStrings.male}
                style={{
                  width: 'auto',
                  display: 'inline-block',
                  marginRight: 8,
                  marginLeft: 8
                }}
                labelStyle={{
                  fontFamily: 'iransans',
                  fontSize: '0.9rem'
                }}
              />
              <RadioButton
                value="female"
                label={userProfileStrings.female}
                labelStyle={{
                  fontFamily: 'iransans',
                  fontSize: '0.9rem'
                }}
                style={{
                  width: 'auto',
                  display: 'inline-block'
                }}
              />
            </RadioButtonGroup>
          </Col>
        </Row>
        {this.props.type === 2
          ? <Row>
              <Col lg={2} md={4} sm={4} xs={6} style={{ padding: '8px' }}>
                <TextField
                  floatingLabelText={userProfileStrings.EnteringYear}
                  value={this.props.userInfo.year}
                  errorText={this.props.errorText.enteringYear}
                  onChange={e => {
                    this.props.errorText.enteringYear = '';
                    if (e.target.value.length < 5) {
                      if (isNumber(e.target.value)) {
                        this.props.handlers.onEnterPersonalData(
                          'year',
                          e.target.value
                        );
                      }
                    }
                  }}
                  onBlur={() => this.props.handlers.verifyEnteringYear()}
                  style={{
                    width: '100%',
                    fontFamily: 'iransans',
                    fontSize: '0.9rem'
                  }}
                />
              </Col>
              <Col
                lg={5}
                md={7}
                sm={7}
                xs={12}
                style={{ padding: '8px', textAlign: 'right' }}
              >
                <div style={{ display: 'inline-block' }}>
                  <p>
                    {userProfileStrings.semester}
                  </p>
                </div>
                <div
                  style={{
                    display: 'inline-block',
                    position: 'relative',
                    top: '26px'
                  }}
                >
                  <DropDownMenu
                    value={this.props.userInfo.semester}
                    onChange={(event, index, value) => {
                      this.props.handlers.onEnterPersonalData(
                        'semester',
                        value
                      );
                    }}
                    autoWidth={true}
                    underlineStyle={{ display: 'none' }}
                    menuItemStyle={{ textAlign: 'left' }}
                  >
                    <MenuItem value={1} primaryText="اول" />
                    <MenuItem value={2} primaryText="دوم" />
                  </DropDownMenu>
                </div>
              </Col>
            </Row>
          : void 0}

        <Row end="xs">
          <Col xs={12}>
            {buttonOrCircular}
          </Col>
        </Row>
      </div>
    );
  };
  // change state to show avatar
  handleMouseEnter = () => {
    this.setState({
      hover: true
    });
  };
  // change state to hide avatar
  handleMouseLeave = () => {
    this.setState({
      hover: false
    });
  };
  renderAvatarSection = () => {
    return (
      <div
        style={
          this.props.windowWidth < 767
            ? { padding: '16px' }
            : {
                padding: '16 px',
                top: 0,
                bottom: 0,
                right: 0,
                left: 0,
                position: 'absolute',
                margin: 'auto',
                height: '200px'
              }
        }
      >
        {this.props.userInfo.avatar.length !== 0
          ? <div
              onMouseEnter={() => {
                this.handleMouseEnter();
              }}
              onMouseLeave={() => {
                this.handleMouseLeave();
              }}
              style={{ position: 'relative', display: 'inline-block' }}
            >
              <Avatar
                style={this.state.hover ? { opacity: '0.5' } : void 0}
                src={
                  this.props.userInfo.avatar.indexOf(
                    process.env.REACT_APP_DOMAIN
                  )
                    ? this.props.userInfo.avatar
                    : process.env.REACT_APP_DOMAIN +
                      process.env.REACT_APP_IMAGES_DIRECTORY +
                      this.props.userInfo.avatar
                }
                size={72}
              />
              <IconButton
                style={
                  this.state.hover
                    ? {
                        position: 'absolute',
                        top: 0,
                        bottom: 0,
                        left: 0,
                        right: 0,
                        margin: 'auto'
                      }
                    : { display: 'none' }
                }
                onTouchTap={() => this.props.handlers.onRemoveAvatar()}
              >
                <Remove />
              </IconButton>
            </div>
          : <Avatar size={72}>
              {this.props.userFirstName}
            </Avatar>}
        <input
          ref="avatarUpload"
          type="file"
          accept=".png,.jpg,.jpeg"
          style={{ display: 'none' }}
          onChange={this.handleAvatarChange}
        />
        <div style={{ marginTop: '16px' }}>
          <RaisedButton
            label={userProfileStrings.editAvatarButton}
            className={this.props.saveButtonClassName}
            primary={true}
            onTouchTap={event => {
              this.refs.avatarUpload.click();
            }}
          />
          <p style={{ padding: 8, fontSize: '0.8rem' }}>
            {userProfileStrings.editAvatar}
          </p>
        </div>
      </div>
    );
  };

  handleAvatarChange = event => {
    this.props.handlers.onAvatarChange(event);
    this.refs.avatarUpload.value = '';
  };
}
export default connect(state => {
  return {
    type: state.user.type,
    windowWidth: state.app.windowWidth
  };
})(PersonalInformation);



