import React from 'react';
import { TableRow as TR, TableRowColumn } from 'material-ui/Table';
import Checkbox from 'material-ui/Checkbox';

export class TableRow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      checked: false
    };
  }

  /**
    * @description : Takes a string html and convert it to real html element
    *
    * @author Armin Eslami
    *
    * @param stringHtml (string) : String html
    *
    * @return html element
    */
  parseHrml = stringHtml => {
    // var parser = new DOMParser();
    // // Creates html dom with head and body tags
    // var parsedHTML = parser.parseFromString(stringHtml, 'text/html');
    // console.log('>', (parsedHTML.body.firstChild).outerHTML);
    // return( (parsedHTML.body.firstChild).outerHTML );

    return <div dangerouslySetInnerHTML={{ __html: stringHtml }} />;
  };

  render() {
    var { item } = this.props;

    return (
      <TR
        hoverable={true}
        onCellClick={e => {
          // If the selected element is not checkbox continue
          if (e.target.tagName !== 'INPUT') {
            // used to be : !$(e.target).is(":checkbox")
            this.props.onTableRowClick
              ? this.props.onTableRowClick(item.id)
              : void 0;
          }
        }}
      >
        {this.props.showCheckbox
          ? <TableRowColumn style={{ width: 30 }}>
              <Checkbox
                ref="checkbox"
                checked={item.isChecked}
                labelStyle={{ display: 'none' }}
                onTouchTap={e => {
                  this.props.onTableBodyCheckboxClick
                    ? this.props.onTableBodyCheckboxClick({
                        id: item.id,
                        isChecked: !item.isChecked
                      })
                    : void 0;
                }}
              />
            </TableRowColumn>
          : null}

        {Object.keys(item).map(keyName => {
          if (
            keyName !== 'id' &&
            keyName !== 'isChecked' &&
            this.props.excludedColumnsOnSmallScreen.indexOf(keyName) === -1
          ) {
            if (
              typeof item[keyName] === 'object' &&
              item[keyName].type === 'html' &&
              item[keyName].value
            ) {
              return (
                <TableRowColumn key={keyName + item.id}>
                  {this.parseHrml(item[keyName].value)}
                </TableRowColumn>
              );
            } else {
              return (
                <TableRowColumn key={keyName + item.id}>
                  {item[keyName]}
                </TableRowColumn>
              );
            }
          }
          return null;
        })}
      </TR>
    );
  }
}

export default TableRow;



